  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #000;
  height: 50%;
  width: 100%;
  border: 5px solid black;
}

.BannerContainer {
  justify-content: center;
  max-width: 75%;
}

.BannerImage {
  width: 100%;
}
